import React, { useState, useEffect } from 'react';
import { createSession, listActiveSessions, getSessionData, updateSessionData, endSession } from './sessionApi';

const SessionManager = ({ onJoinSession, currentSessionId, onLeaveSession, onSetupPlayers, onClose }) => {
  const [sessions, setSessions] = useState([]);
  const [loading, setLoading] = useState(false);
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [showJoinModal, setShowJoinModal] = useState(false);
  const [newSessionName, setNewSessionName] = useState('');
  const [sessionIdToJoin, setSessionIdToJoin] = useState('');

  // 
  const loadSessions = async () => {
    setLoading(true);
    try {
      const result = await listActiveSessions();
      if (result.status === 'success') {
        setSessions(result.sessions || []);
      } else {
        alert(': ' + (result.message || ''));
        console.error(':', result.message);
      }
    } catch (error) {
      alert('');
      console.error(':', error);
    }
    setLoading(false);
  };

  // 
  const handleCreateSession = async () => {
    if (!newSessionName.trim()) {
      alert('');
      return;
    }

    setLoading(true);
    try {
      const result = await createSession(newSessionName.trim());
      if (result.status === 'success') {
        alert(` ID: ${result.sessionId}`);
        setShowCreateModal(false);
        setNewSessionName('');
        loadSessions();
      } else {
        alert(': ' + (result.message || ''));
      }
    } catch (error) {
      console.error(':', error);
      alert('');
    }
    setLoading(false);
  };

  // 
  const handleJoinSession = async (sessionId) => {
    if (!sessionId) {
      alert(' ID');
      return;
    }

    setLoading(true);
    try {
      const result = await getSessionData(sessionId);
      if (result.status === 'success') {
        onJoinSession(sessionId, result.gameData);
        setShowJoinModal(false);
        setShowCreateModal(false);
        if (typeof onClose === 'function') onClose();
      } else {
        alert(': ' + (result.message || ''));
      }
    } catch (error) {
      console.error(':', error);
      alert('');
    }
    setLoading(false);
  };

  // 
  const handleJoinSessionFromList = async (sessionId) => {
    setLoading(true);
    try {
      const result = await getSessionData(sessionId);
      if (result.status === 'success') {
        onJoinSession(sessionId, result.gameData);
        setShowJoinModal(false);
        setShowCreateModal(false);
        if (typeof onClose === 'function') onClose();
      } else {
        alert(': ' + (result.message || ''));
      }
    } catch (error) {
      console.error(':', error);
      alert('');
    }
    setLoading(false);
  };

  // 
  const handleEndSession = async (sessionId) => {
    if (!confirm('')) return;

    setLoading(true);
    try {
      const result = await endSession(sessionId);
      if (result.status === 'success') {
        alert('');
        loadSessions();
        if (currentSessionId === sessionId) {
          onLeaveSession();
        }
      } else {
        alert(': ' + (result.message || ''));
      }
    } catch (error) {
      console.error(':', error);
      alert('');
    }
    setLoading(false);
  };

  useEffect(() => {
    loadSessions();
  }, []);

  return (
    <div style={{ padding: '20px', maxWidth: '800px', margin: '0 auto' }}>
      <h2 style={{ color: '#1976d2', marginBottom: '20px' }}></h2>
      
      {/*  */}
      {currentSessionId && (
        <div style={{ 
          background: '#e3f2fd', 
          padding: '15px', 
          borderRadius: '8px', 
          marginBottom: '20px',
          border: '1px solid #2196f3'
        }}>
          <h3 style={{ margin: '0 0 10px 0', color: '#1976d2' }}></h3>
          <p style={{ margin: '0 0 10px 0' }}> ID: <strong>{currentSessionId}</strong></p>
          <div style={{ display: 'flex', gap: '10px' }}>
            <button
              onClick={onSetupPlayers}
              style={{
                padding: '8px 16px',
                background: '#4caf50',
                color: 'white',
                border: 'none',
                borderRadius: '4px',
                cursor: 'pointer'
              }}
            >
              
            </button>
            <button
              onClick={onLeaveSession}
              style={{
                padding: '8px 16px',
                background: '#f44336',
                color: 'white',
                border: 'none',
                borderRadius: '4px',
                cursor: 'pointer'
              }}
            >
              
            </button>
          </div>
        </div>
      )}

      {/*  */}
      <div style={{ display: 'flex', gap: '10px', marginBottom: '20px' }}>
        <button
          onClick={() => setShowCreateModal(true)}
          style={{
            padding: '10px 20px',
            background: '#4caf50',
            color: 'white',
            border: 'none',
            borderRadius: '4px',
            cursor: 'pointer'
          }}
        >
          
        </button>
        <button
          onClick={() => setShowJoinModal(true)}
          style={{
            padding: '10px 20px',
            background: '#2196f3',
            color: 'white',
            border: 'none',
            borderRadius: '4px',
            cursor: 'pointer'
          }}
        >
          
        </button>
        <button
          onClick={loadSessions}
          disabled={loading}
          style={{
            padding: '10px 20px',
            background: '#ff9800',
            color: 'white',
            border: 'none',
            borderRadius: '4px',
            cursor: loading ? 'not-allowed' : 'pointer',
            opacity: loading ? 0.6 : 1
          }}
        >
          {loading ? '...' : ''}
        </button>
      </div>

      {/*  */}
      <div>
        <h3 style={{ color: '#333', marginBottom: '15px' }}></h3>
        {loading ? (
          <div style={{ textAlign: 'center', padding: '20px' }}>...</div>
        ) : sessions.length === 0 ? (
          <div style={{ textAlign: 'center', padding: '20px', color: '#666' }}>
            
          </div>
        ) : (
          <div style={{ display: 'grid', gap: '10px' }}>
            {sessions.map(session => (
              <div
                key={session.sessionId}
                style={{
                  border: '1px solid #ddd',
                  borderRadius: '8px',
                  padding: '15px',
                  background: currentSessionId === session.sessionId ? '#e8f5e8' : '#fff'
                }}
              >
                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                  <div>
                    <h4 style={{ margin: '0 0 5px 0', color: '#333' }}>
                       ID: {session.sessionId}
                    </h4>
                    <p style={{ margin: '0 0 5px 0', color: '#666', fontSize: '14px' }}>
                      : {session.createdBy}
                    </p>
                    <p style={{ margin: '0', color: '#666', fontSize: '14px' }}>
                      : {new Date(session.createdTime).toLocaleString('zh-TW')}
                    </p>
                  </div>
                  <div style={{ display: 'flex', gap: '10px' }}>
                    {currentSessionId !== session.sessionId && (
                      <button
                        onClick={() => handleJoinSessionFromList(session.sessionId)}
                        style={{
                          padding: '6px 12px',
                          background: '#2196f3',
                          color: 'white',
                          border: 'none',
                          borderRadius: '4px',
                          cursor: 'pointer',
                          fontSize: '12px'
                        }}
                      >
                        
                      </button>
                    )}
                    <button
                      onClick={() => handleEndSession(session.sessionId)}
                      style={{
                        padding: '6px 12px',
                        background: '#f44336',
                        color: 'white',
                        border: 'none',
                        borderRadius: '4px',
                        cursor: 'pointer',
                        fontSize: '12px'
                      }}
                    >
                      
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      {/*  Modal */}
      {showCreateModal && (
        <div style={{
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: 'rgba(0,0,0,0.5)',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          zIndex: 1000
        }}>
          <div style={{
            background: 'white',
            padding: '30px',
            borderRadius: '8px',
            minWidth: '400px',
            maxWidth: '500px'
          }}>
            <h3 style={{ margin: '0 0 20px 0', color: '#1976d2' }}></h3>
            <input
              type="text"
              value={newSessionName}
              onChange={(e) => setNewSessionName(e.target.value)}
              placeholder=""
              style={{
                width: '100%',
                padding: '10px',
                border: '1px solid #ddd',
                borderRadius: '4px',
                fontSize: '16px',
                marginBottom: '20px'
              }}
            />
            <div style={{ display: 'flex', gap: '10px', justifyContent: 'flex-end' }}>
              <button
                onClick={() => setShowCreateModal(false)}
                style={{
                  padding: '10px 20px',
                  background: '#666',
                  color: 'white',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: 'pointer'
                }}
              >
                
              </button>
              <button
                onClick={handleCreateSession}
                disabled={loading}
                style={{
                  padding: '10px 20px',
                  background: '#4caf50',
                  color: 'white',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: loading ? 'not-allowed' : 'pointer'
                }}
              >
                {loading ? '...' : ''}
              </button>
            </div>
          </div>
        </div>
      )}

      {/*  Modal */}
      {showJoinModal && (
        <div style={{
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: 'rgba(0,0,0,0.5)',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          zIndex: 1000
        }}>
          <div style={{
            background: 'white',
            padding: '30px',
            borderRadius: '8px',
            minWidth: '400px',
            maxWidth: '500px'
          }}>
            <h3 style={{ margin: '0 0 20px 0', color: '#1976d2' }}></h3>
            <input
              type="text"
              value={sessionIdToJoin}
              onChange={(e) => setSessionIdToJoin(e.target.value)}
              placeholder=" ID"
              style={{
                width: '100%',
                padding: '10px',
                border: '1px solid #ddd',
                borderRadius: '4px',
                fontSize: '16px',
                marginBottom: '20px'
              }}
            />
            <div style={{ display: 'flex', gap: '10px', justifyContent: 'flex-end' }}>
              <button
                onClick={() => setShowJoinModal(false)}
                style={{
                  padding: '10px 20px',
                  background: '#666',
                  color: 'white',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: 'pointer'
                }}
              >
                
              </button>
              <button
                onClick={() => handleJoinSession(sessionIdToJoin.trim())}
                disabled={loading}
                style={{
                  padding: '10px 20px',
                  background: '#2196f3',
                  color: 'white',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: loading ? 'not-allowed' : 'pointer'
                }}
              >
                {loading ? '...' : ''}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default SessionManager; 